@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    font-family: Roboto, helvetica, sans-serif !important;
    font-size: 18px;
}
/* Home Page Styles */

.home-nav{
    background-color: #060D2B !important;
    padding-left: 45px;
    padding-right: 45px;
}

.home-nav li{
    padding: 0px 20px;
}

a#get-started{
    color: white;
    border: 2px solid white;
    border-radius: 5px;
    padding: 10px 15px;
    text-transform: capitalize;
}

.layout{
    background: rgb(232, 240, 243);
}

#cover{
    position: absolute;
    top: 0px;
    left: 0px;
    min-width: 100vw;
    min-height: 480px;
    background: black;
    opacity: .3;
    z-index: 3;
}

.caption{
    z-index: 5 !important;
}

.container.news-section{
    margin-top: 50px !important;
    margin-bottom: 50px;
}

.news-section-heading{
    /* position: relative; */
    text-align: center;
    margin-bottom: 40px;
}

.news-section-heading h2{
    font-size: 1.4em !important;
    font-weight: bolder;
    text-align: center;
    background: white;
    padding: 5px 10px;
    display: inline-block;
}

.news-section-heading hr{
    width: 50%;
    text-align: center;
    position: relative;
    top: 62px;
    z-index: -1;
}


/* News Card Styling */

.card{
    border-radius: 10px !important;
}

.card img{
    border-radius: 10px 10px 0px 0px !important;
}

.card-content .description{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.card-content *,
.card-action *{
    font-size: 12px !important;
}

.card-content .description .news-type{
    font-weight: bold !important;
    border-bottom: 2px solid rgb(199, 199, 199);
    text-transform: capitalize;
    padding: 5px;
}

.card-content .description .news-time{
    color: rgb(167, 167, 167);
    font-size: 10px !important;
    font-weight: normal !important;
    position: relative;
    top: 5px;
}

.card-content h4{
    font-size: 14px;
    font-weight: bold;
}

/* Dashoard Info Section */

.dashboard-info-section{
    background-color: #060D2B;
    border-radius: 10px;
    padding: 40px;
    color: white;
    width: 85%;
    margin: 30px auto;
}

.dashboard-info-section input{
    border: none !important;
    background: gray !important;
    padding-left: 10px !important;
    width: 300px !important;
    display: block;
    margin: 20px 0px !important;
}

.dashboard-info-section h2{
    font-size: 32px;
    margin-top: 10px !important;
}

.dashboard-info-section p,
.dashboard-info-section tr{
    font-size: 14px;
}

.dashboard-info-section button{
    padding: 10px 15px;
    background: #249921;
    font-weight: 600;
    border: none;
    color: white;
    font: inherit;
}

.dashboard-info-section table{
    margin-bottom: 40px !important;
}

.dashboard-info-section thead{
    background: #1D2A61;
}

.warning{
    color: #FF5959;
}

.dashboard-info-section button:hover{
    cursor: pointer;
}

#topic{
    font-family: Rubikone;
    font-size: 36px;
    padding: 20px 80px !important;
    font-weight: 600;
}

/* Dashboard Info Section End */

/* Footer Styling */

footer.home-footer{
    padding: 40px 20px;
    display: flex;
    justify-content: space-evenly;
    color: white;
    background: #060D2B;
    /* margin-top: 40px; */
}

.home-footer a{
    color: inherit;
}

#to-top{
    width: 35px;
    height: 35px;
    max-width: 50px;
    max-height: 50px;
    position: fixed;
    bottom: 80px;
    right: 25px;
    background: #060D2B;
}

#to-top:hover{
    cursor: pointer;
    background: rgb(17, 24, 56);
}

#to-top img{
    width: inherit;
    height: inherit;
    padding: 5px;
}
#loader{
    width: 180px;
    height: 180px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
    font-size: 28px;
    text-align: center;
    margin: auto;
    z-index: 1001;
}

#loader:before{
    content: "";
    display: block;
    position: absolute;
    width: inherit;
    height: inherit;
    border-radius: 50%;
    border-top: 2px solid #152c91;
    border-bottom: 2px solid #152c91;
    border-left: 2px solid gray;
    border-right: 2px solid gray;
    top: 0px;
    left: 0px;
    transform-origin: 50% 50%;
    -webkit-animation: 5s ease-in-out infinite forwards rotate-bounce;
            animation: 5s ease-in-out infinite forwards rotate-bounce;
}

#loader-background{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 999999;
}

#loader-background::before{
    content: "";
    display: block;
    min-width: 50vw;
    min-height: 100vh;
    background: #060D2B;
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 999;
}

#loader-background::after{
    content: "";
    display: block;
    min-width: 50vw;
    min-height: 100vh;
    background: #060D2B;
    position: fixed;
    right: 0px;
    top: 0px;
    z-index: 999;
}

.message{
    text-transform: capitalize !important;
}


#form-loader{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, .5);
    width: 100vw;
    height: 100vh;
    top: 0px;
    left: 0px;
    position: fixed;
    z-index: 40;
}

#form-loader img{
    width: 180px;
    height: 180px;
}



@-webkit-keyframes rotate-bounce{
    0%{
        transform: rotateZ(0deg) scale(1);
    }
    50%{
        transform: rotateZ(360deg) scale(1.3);
    }
    100%{
        transform: rotateZ(-360deg) scale(1);
    }
}



@keyframes rotate-bounce{
    0%{
        transform: rotateZ(0deg) scale(1);
    }
    50%{
        transform: rotateZ(360deg) scale(1.3);
    }
    100%{
        transform: rotateZ(-360deg) scale(1);
    }
}
