    font-size: 24px;
}

.game-holder{
    background: url(/static/media/game_room_background.0efc8925.jpg);
    background-size: cover;
    background-position: center center;
}

.game-holder > section{
    width: 90%;
    margin-bottom: 0px !important;
}


.game-card{
    padding: 40px 20px !important;
    text-align: center;
    width: 100%;
    min-height: 450px;
    position: relative;
}

.game-card h4{
    font-size: 24px;
}

.game-card p{
    font-size: 14px;
}

.game-card img{
    width: 80px;
}

.game-card button{
    margin-top: 20px;
    margin-right: 10px;
    font-size: 14px;
    
}

.game-card a{
    position: absolute;
    bottom: 25px;
    right: 25px; 
}

.game-card .container{
    width: 85%;
