}

.game-room-img{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    position: relative;
    top: 10px;
}

#floating-menu{
    position: absolute;
    top: 80px;
    right: 30px;
    background-color: white;
    border-radius: 10px;
    transform-origin: 100% 0px;
    transition: .2s ease-in-out transform; 
    width: 400px;
}

#floating-menu > div {
    padding: 20px;
    border-bottom: 1px solid black;
}

.shrink-out{
    transform: scale(0);
}

.pop-in{
    transform: scale(1);
}

#exit{
    border-bottom: none !important;
    margin-bottom: 0px !important;
}

#game-link{
    position: fixed;
    right: 25px;
    top: calc(50vh - 40px);
    padding: 17px 20px;
    background: #060D2B;
    border-radius: 50%;
    transition: .5s ease-in background;
